@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top-grid {
  justify-self: center;
  align-self: center;
  width: 100vw;  /* fallback for old browsers */
/*background: -webkit-linear-gradient(to right, #FFFFFF, #FC466B);  /* Chrome 10-25, Safari 5.1-6 */
/*background: linear-gradient(to right, #FFFFFF, #FC466B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

body {
